import React from 'react'
import Styles from './Styles'
import { Form, Field } from 'react-final-form'
import emailjs from 'emailjs-com';
import swal from 'sweetalert';


function FormInt() {



  function sendEmail(e) {
    e.preventDefault();

    emailjs.sendForm('service_y2e1mhr', 'template_oftphsj', e.target, 'jTuxy0DvvhLnqvnnp')
      .then((result) => {
          console.log(result.text);
      }, (error) => {
          console.log(error.text);
      });
      swal({
  title: "Mensaje enviado",
  text: "En breve nos pondremos en contacto con usted",
  icon: "success",
  button: "Cerrar",
});;
      e.target.reset();
  }

const sleep = ms => new Promise(resolve => setTimeout(resolve, ms))

const onSubmit = async values => {
  await sleep(300)
  document.getElementById("contacto").reset();
}


return (
  <Styles >
    <Form
      onSubmit={onSubmit}
      initialValues={{ }}
    >
      {({ handleSubmit, form, submitting, pristine, values }) => (
        <form id="contacto" onSubmit={sendEmail}>
          <div  >
            <label>Nombre </label>
            <input  name="nombre" component="input" type="text" placeholder="Nombre Completo" required/>
            
          </div>
          <div>
            <label>Empresa</label>
            <input name="empresa" component="input" type="text" placeholder="Nombre de la compaa" required/>
            
          </div>
         
          <div>
            <label>Telfono</label>
            <input name="telefono" component="input" type="text" placeholder="Oficina" required/>
            
          </div>
          
          
          <div>
            <label>E-mail</label>
            <input name="email" component="input" type="email" placeholder="*****@****.com" required/>
            
          </div>

           <div>
            <label>Mensaje</label>
            <Field name="notes" component="textarea" placeholder="Cmo podemos apoyarle?" />
          </div>
          
          <div className="buttons">
            <button type="submit"  disabled={submitting}>
              Enviar
            </button>
          </div>
         
        </form>
      )}
    </Form>
  </Styles>
)
      }

export default FormInt;
